body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #f4e6f7;
  padding: 0 15px;
  font-family: Courier New, Courier, monospace;
  line-height: 1.5;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.thumb {
  height: 240px;
}

.wrapper {
  max-width: 480px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.wrapper__search {
  background-color: #8d31ef;
  border-radius: 15px;
  margin-top: 10px;
  margin-bottom: 30px;
  padding: 10px;
}

.btn {
  color: #fafafa;
  max-width: 120px;
  background-color: #212121;
  border: 1px solid #212121;
  border-radius: 25px;
  padding: 9px;
  font-family: inherit;
  font-size: 14px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 3px 3px 8px 2px #0000004d;
}

.btn:hover {
  color: #212121;
  background-color: #fafafa;
}

.load-more {
  margin-bottom: 15px;
}

.search-form {
  flex-direction: column;
  align-items: center;
  gap: 15px;
  display: flex;
}

.input {
  border: 1px solid #212121;
  border-radius: 25px;
  padding: 8px;
  font-family: inherit;
  font-size: 14px;
  box-shadow: 3px 3px 8px 2px #0000004d;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  margin-bottom: 20px;
  display: flex;
}

.photo-card {
  width: 100%;
  background-color: #e6e6fa;
  border: 1px solid gray;
  border-radius: 10px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
}

.photo-card:hover {
  box-shadow: 6px 6px 8px 2px #0000001a;
}

.info-item {
  margin: 0;
}

.info {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 9px;
  padding: 10px;
  font-size: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .photo-card {
    flex-basis: calc(50% - 20px);
  }

  .gallery {
    gap: 20px;
  }

  .search-form {
    flex-direction: row;
    gap: 50px;
  }

  .wrapper {
    max-width: 960px;
  }

  .wrapper__search {
    padding: 15px;
  }
}

@media screen and (min-width: 1024px) {
  .photo-card {
    flex-basis: calc(33.3333% - 10px);
  }

  .gallery {
    gap: 15px;
  }

  .wrapper {
    max-width: 1200px;
  }
}

